.input--group {
    display: flex;
    flex-wrap: nowrap;
    border-bottom: 1px solid hsl(var(--black)/.2);
    .input-group-text {
        border: none;
        padding-left: 0;
        padding-right: 10px;
        background: transparent;
        border-radius: 0;
        color: rgb(var(--base));
    }
    .form--control {
        border: none;
        background: transparent;
    }
    .form--select-light {
        border: none;
    }
}