.sidebar {
    position: sticky;
    top: 100px;
    &-nav {
      .list__item {
        border-bottom: 1px dashed rgb(var(--base));
      }
      &__link {
        display: block;
        padding-top: 10px;
        padding-bottom: 10px;
        border-right: 3px solid transparent;
        color: hsl(var(--accent));
        font-weight: 500;
        transition: all 0.3s ease;
        &:hover {
          color: rgb(var(--base));
          border-right: 2px solid rgb(var(--base));
        }
        &.active {
          border-right: 2px solid rgb(var(--base));
          color: rgb(var(--base));
        }
      }
    }
  }
  
  