.btn {
  font-weight: 500;
  transition: all 0.3s ease;
  border-radius: 3px;
  &:hover {
    box-shadow: 0 8px 15px hsl(var(--dark) / 0.4);
  }
  // Color Option
  &--primary {
    background: hsl(var(--primary));
    color: hsl(var(--white));
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--primary));
    }
  }
  &--secondary {
    background: hsl(var(--secondary));
    color: hsl(var(--white));
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--secondary));
    }
  }
  &--success {
    background: hsl(var(--success));
    color: hsl(var(--white));
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--success));
    }
  }
  &--danger {
    background: hsl(var(--danger));
    color: hsl(var(--white));
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--danger));
    }
  }
  &--warning {
    background: hsl(var(--warning));
    color: hsl(var(--white));
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--warning));
    }
  }
  &--info {
    background: hsl(var(--info));
    color: hsl(var(--white));
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--info));
    }
  }
  &--dark {
    background: hsl(var(--dark));
    color: hsl(var(--white));
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--dark));
    }
  }
  &--light {
    background-color: hsl(var(--white));
    color: hsl(var(--primary));
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--dark));
    }
  }
  &--base {
    background: rgb(var(--base));
    color: hsl(var(--white));
    &:hover {
      color: hsl(var(--white));
      background: rgb(var(--base));
    }
  }
  // Button  Shape
  &--sqr {
    line-height: 1;
    padding: 0.75rem;
    font-size: 20px;
  }
  // Button Size
  &--sm {
    padding: 0.3rem 1rem; //8px X 16px
  }
  &--md {
    padding: 0.5rem 1rem; //8px X 16px
  }
  &--lg {
    padding: 0.625rem 1.25rem; //10px X 20px
  }
  &--xl {
    padding: 0.75rem 1.5rem; //12px X 24px
  }
  &--xxl {
    padding: 1rem 2rem; //16px X 24px
  }
  // Button Outline
  &--outline {
    border: 1px solid hsl(var(--border));
    background: transparent;
    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--primary));
      border-color: hsl(var(--primary));
    }
    &.active {
      color: hsl(var(--white));
      background: hsl(var(--primary));
      border-color: hsl(var(--primary));
    }
  }
  // Custom Button
  &--custom {
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 30px;
    padding-right: 30px;
    background: transparent;
    border: 2px solid rgb(var(--base));
    color: rgb(var(--base));
    &:hover {
      color: rgb(var(--base));
      background-color: hsl(var(--white));
      border: 2px solid rgb(var(--base));
    }
    &.active {
      background-color: rgb(var(--base));
      color: hsl(var(--white));
    }
  }
  // Login Button
  &--login {
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 30px;
    padding-right: 30px;
    background: rgb(var(--base));
    border: 2px solid rgb(var(--base));
    color: hsl(var(--white));
    @include lg-screen {
      padding: 0;
      border: none;
      background: transparent;
    }
    &:hover {
      background: rgb(var(--base));
      border: 2px solid rgb(var(--base));
      color: hsl(var(--white));
      @include lg-screen {
        padding: 0;
        border: none;
        background: transparent;
        box-shadow: none;
        color: rgb(var(--base));
      }
    }
  }
}
