.testimonial-slider {
  padding: 30px 15px;
  border-radius: 10px;
  box-shadow: 0 0 15px hsl(var(--accent) / 0.1);
  @include md-screen {
    box-shadow: 0 0 50px hsl(var(--accent) / 0.1);
    padding: 60px 30px;
    border-radius: 25px;
  }
  @include lg-screen {
    padding: 60px;
  }
  &__icon {
    font-size: var(--h1);
    color: rgb(var(--base));
  }
  &__body {
    &-text {
      font-size: var(--h6);
      color: hsl(var(--black-50));
      max-width: 55ch;
      margin-left: auto;
      margin-right: auto;
      @include md-screen {
        font-size: var(--h5);
      }
    }
  }
  &__btn {
    @include md-screen {
      border: none;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      isolation: isolate;
      width: 74px;
      height: 74px;
      aspect-ratio: 1;
      border-radius: 50%;
      background: hsl(var(--white));
      box-shadow: 0 0 15px hsl(var(--black) / 0.1);
      &--prev {
        left: -20px;
      }
      &--next {
        right: -20px;
      }
    }
    @include lg-screen {
        &--prev {
            left: -34px;
          }
          &--next {
            right: -34px;
          }
    }
    &-content {
      @include md-screen {
        position: absolute;
        inset: 10px;
        border-radius: 50%;
        font-size: 22px;
        line-height: 1;
        display: grid;
        place-items: center;
        color: rgb(var(--base));
        background: rgb(var(--base) / 0.3);
        transition: all 0.3s ease;
        &:hover {
          color: hsl(var(--heading));
        }
      }
    }
  }
}
