.process {
    padding: 15px;
    height: 100%;
    border-radius: 10px;
    @include xlii-screen {
        padding-inline: 40px;
    }
  &__icon {
    margin: 10px;
    position: relative;
    isolation: isolate;
    box-shadow: 0 0 20px hsl(var(--black) / 0.07);
    &::before {
      content: "";
      position: absolute;
      inset: -15px;
      border-radius: 50%;
      outline: 1px dashed hsl(var(--black) / 0.2);
    }
    &-shape {
        @include lg-screen {
            &::after {
                content: '';
                position: absolute;
                top: 50%;
                left: calc(100% + 30px);
                transform: translateY(-50%);
                width: 100px;
                height: 30px;
                background-image: url(../images/process-arrow.png);
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
            }
        }
        @include xl-screen {
            &::after {
                width: 145px;
            }
        }
        @include xlii-screen {
            &::after {
                width: 150px;
                left: calc(100% + 50px);
            }
        }
    }
  }
}
