.user-balance {
    padding: 30px 15px;
    background: rgb(var(--base));
    border-radius: 5px;
    text-align: center;
    height: 100%;
    @include md-screen {
        border-radius: 0 5px 5px 0;
        padding: 40px 20px;
    }
    &__icon {
        background: hsl(var(--black-50)/.3);
        color: hsl(var(--white));
    }
}