.form--control {
  height: 50px;
  line-height: 40px;
  border-radius: 3px;
  font-size: 14px;
  border: 1px solid hsl(var(--border)/.5);
  background: hsl(var(--light-400));
  color: hsl(var(--accent));
  transition: all 0.3s ease;
  &::placeholder {
    text-transform: capitalize;
    color: hsl(var(--accent)/.8);
  }
  &:focus {
    outline: none;
    border: 1px solid hsl(var(--border)/.5);
    background: hsl(var(--light-400));
    box-shadow: none;
  }
  &:-webkit-autofill {
    -webkit-text-fill-color: hsl(var(--accent)) !important;
    -webkit-box-shadow: 0 0 0px 1000px hsl(var(--light-400)) inset;
  }

  &-textarea {
    padding-top: 15px;
    border-radius: 3px;
    font-size: 14px;
    border: 1px solid hsl(var(--border)/.5);
    background: hsl(var(--light-400));
    color: hsl(var(--accent));
    transition: all 0.3s ease;
    &::placeholder {
      text-transform: capitalize;
      color: hsl(var(--accent)/.8);
    }
    &:focus {
      outline: none;
      border: 1px solid hsl(var(--border)/.5);
      background: hsl(var(--light-400));
      box-shadow: none;
    }
  }
  &[readonly] {
    background: hsl(var(--light-400));
    border: 1px solid hsl(var(--border)/.5);
    color: hsl(var(--dark));
  }
  &[disabled] {
    background: hsl(var(--light-400));
    border: 1px solid hsl(var(--border)/.5);
    color: hsl(var(--dark));
  }
  &[type="file"] {
    line-height: 38px;
  }
}
.input-group-text {
  border: 1px solid hsl(var(--border)/.5);
  border-right: none;
  background: hsl(var(--light-600));
  color: hsl(var(--text));
  padding-inline: 20px;
}
