.user-profile {
    padding: 30px 15px;
    border: 1px solid hsl(var(--border));
    background: hsl(var(--white));
    border-radius: 5px;
    text-align: center;
    height: 100%;
    @include md-screen {
        border-radius: 5px 0 0 5px;
        padding: 40px 20px;
    }
    &__nav {
        display: flex;
        align-items: center;
        gap: 1rem;
    }
    &__btn {
        color: rgb(var(--base));
        background: rgb(var(--base)/.2);
        &:hover {
            color: hsl(var(--white));
            background: rgb(var(--base));
        }
    }
}