.client-section {
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: -217px;
    position: relative;
    z-index: 3;
    @include md-screen {
        margin-top: -137px;
    }
    @include lg-screen {
        margin-top: -70px;
        border-radius: 10px;
        box-shadow: 0 0 30px hsl(var(--dark)/.1);
        background: hsl(var(--white));
    }
    &__head {
        @include md-screen {
            text-align: end;
            padding-right: 30px;
            position: relative;
            &::after {
                content: '';
                width: 3px;
                height: 100%;
                background-color: rgb(var(--base));
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
    &__sub-title {
        display: block;
        color: hsl(var(--accent));
        letter-spacing: .03em;
        font-size: var(--h5);
        font-family: var(--heading-font);
        font-weight: 600;
    }
}