.blog-post {
    position: relative;
    isolation: isolate;
    &__img {
        display: block;
        overflow: hidden;
        border-radius: 10px 10px 0 0;
        &-is {
            width: 100%;
            height: 300px;
            object-fit: cover;
        }
    }
    &__body {
        padding: 30px 15px;
        background: hsl(var(--white));
        border-radius: 0 0 10px 10px;
        box-shadow: 0 25px 40px hsl(var(--black)/.05);
        @include xl-screen {
            padding: 35px 30px;
        }
    }
    &__title {
        color: hsl(var(--heading));
        &:hover {
            color: rgb(var(--base));
        }
    }
}