.dash-card {
  padding: 15px;
  border: 1px solid hsl(var(--border));
  border-radius: 5px;
  background: hsl(var(--white));
  height: 100%;
  &__icon {
    display: inline-block;
    border-radius: 37% 63% 75% 25% / 56% 61% 39% 44%;
    background: rgb(var(--base)/.3);
    color: rgb(var(--base));
    transition: all 0.3s ease;
    flex-shrink: 0;
  }
  &__btn {
      display: inline-block;
      padding: 5px 15px;
      font-size: 12px;
      font-weight: 700;
      font-family: var(--heading-font);
      color: hsl(var(--text));
      border: 1px solid hsl(var(--border));
      border-radius: 2px;
      &:hover {
        background: rgb(var(--base));
        color: hsl(var(--white));
        border-color: rgb(var(--base));
      }
  }
}
