.blog-section {
    padding-bottom: clamp(calc(60px + 217px), 8vw, calc(120px + 217px));
    @include md-screen {
        padding-bottom: clamp(calc(60px + 137px), 8vw, calc(120px + 137px));
    }
    @include lg-screen {
        padding-bottom: clamp(calc(60px + 70px), 8vw, calc(120px + 70px));
    }
}