.contact-info {
    display: flex;
    background: hsl(var(--white));
    padding: 30px 15px;
    border-radius: 5px;
    gap: 1rem;
    align-items: flex-start;
    @include xl-screen {
        padding: 60px 30px;
    }
    @include xlii-screen {
        padding: 60px 40px;
    }
    &__icon {
        display: grid;
        place-items: center;
        padding-inline: 20px;
        aspect-ratio: 1;
        font-size: 24px;
        background: rgb(var(--base));
        border-radius: 50%;
        color: hsl(var(--white));

        &-border {
            flex-shrink: 0;
            display: inline-block;
            padding: 8px;
            border: 1px dashed rgb(var(--base));
            background: hsl(var(--white));
            border-radius: 50%;
        }
    }
    &-nav {
        @include lg-screen {
            background: hsl(var(--white));
            border-radius: 15px;
            box-shadow: 0 0 30px hsl(var(--black)/.01);
        }
    }
}