.login-section {
    padding-bottom: clamp(calc(90px + 217px), 8vw, calc(180px + 217px));
    @include md-screen {
        padding-bottom: clamp(calc(90px + 137px), 8vw, calc(180px + 137px));
    }
    @include lg-screen {
        padding-bottom: clamp(calc(120px + 70px), 8vw, calc(210px + 70px));
        
    }
    &__content {
        @include lg-screen {
            border-radius: 15px;
            padding-top: clamp(30px, 4vw, 60px);
            background: rgb(var(--base));
            padding-inline: 30px;
            position: relative;
            
        }
        @include xl-screen {
            padding-inline: 40px;
        }
        @include xlii-screen {
            padding-inline: 80px;
        }
    }
    &__image {
        @include lg-screen {
            position: sticky;
            top: 100px;
            isolation: isolate;
            &::after {
                content: '';
                width: 650px;
                height: 650px;
                border-radius: 50%;
                position: absolute;
                left: 0;
                bottom: 0;
                background: hsl(var(--white)/.2);
                z-index: -1;
            }
        }
        @include xl-screen {
            &::after {
                left: -40px;
            }
        }
        @include xlii-screen {
            &::after {
                left: -80px;
            }
        }
    }
}