.blog-details {
    &__img {
        width: 100%;
        &-is {
            border-radius: 10px;
            max-width: 100%;
            height: auto;
            @include md-screen {
                width: 100%;
                height: 350px;
                object-fit: cover;
            }
            @include xl-screen {
                height: 500px;
            }
            @include xlii-screen {
                height: 600px;
            }
        }
    }
}