.login-form {
    @include lg-screen {
        position: relative;
        top: clamp(30px, 4vw, 60px);
        padding: 50px 30px;
        background: hsl(var(--white));
        border-radius: 15px;
        box-shadow: 0 0 30px hsl(var(--accent)/.1);
    }
    @include lg-screen {
        padding: 60px 40px;
    }
    @include xlii-screen {
        padding: 60px 45px;
    }
}