.client-slider {
    &__item {
        padding-left: 10px;
        padding-right: 10px;
        &:focus {
            outline: none;
        }
    }
    &__img {
        max-width: 100%;
        height: 70px;
        margin-left: auto;
        margin-right: auto;
        object-fit: contain;
    }
}