.service-card {
  padding: 30px;
  position: relative;
  isolation: isolate;
  background: hsl(var(--white));
  box-shadow: 0 -30px 100px hsl(var(--dark) / 0.03);
  border-radius: 10px;
  transition: all 0.3s ease;
  overflow: hidden;
  &:hover {
    background: rgb(var(--base));
    color: hsl(var(--white));
    .service-card__icon {
      background: hsl(var(--white));
      color: rgb(var(--base));
    }
    .service-card__title {
      color: hsl(var(--white));
    }
    &::after {
      opacity: 1;
      visibility: visible;
    }
  }
  @include xl-screen {
    padding-inline: 15px;
  }
  &::after {
    content: "";
    width: 80px;
    aspect-ratio: 1;
    border-radius: 50%;
    background: hsl(var(--black-50)/.1);
    outline: 30px solid hsl(var(--black-50)/.05);
    box-shadow: 0 0 15px rgb(var(--base)/.5);
    position: absolute;
    top: -15px;
    left: -15px;
    z-index: -1;
    transition: all 0.3s ease;
    opacity: 0;
    visibility: hidden;
  }
  &__icon {
    border-radius: 37% 63% 75% 25% / 56% 61% 39% 44%;
    background: rgb(var(--base));
    color: hsl(var(--white));
    transition: all 0.3s ease;
    flex-shrink: 0;
  }
  &__title {
    transition: all 0.3s ease;
  }
}
